.form-control{
    font-size: 14px;
    background-color: #fff;
    border: 1px solid black;
    height: 45px;
    border-radius: 0;
    @include transition();

    &::placeholder{
        color: rgba(24, 24, 24, 0.76);
    }

    &:focus, &:active{
        outline: none;
        border-color: black;
        box-shadow: none;
    }
}

textarea.form-control{
    height: auto;
}